import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ExerciseService } from '../services/exercise.service';
import { Training } from '../models';

@Injectable({
  providedIn: 'root'
})
export class TrainingsService {

  Trainings:any = [];
  index:number = 0;
  constructor(private route: Router, private exService:ExerciseService) {}


  getTrainings(): Training[] {
    this.Trainings[0] = new Training(0);
    this.Trainings[1] = new Training(1);
    this.Trainings[2] = new Training(2);
    this.Trainings[3] = new Training(3);
    this.index = 3;
    return this.Trainings;
  }
  
  getTraining(){
    const tr = this.getTrainings();
    if(typeof tr[(this.route.url.split('/').slice(-1)[0])] === 'number'){
      return tr[(this.route.url.split('/')[1])];
    }
    return tr[(this.route.url.split('/')[2])];
  }
  
  addTraining(){
    this.index++;
    this.Trainings[this.index] = new Training(this.index);
  }
}
